@mixin dMax {
  @media (max-width: 1200px) {
    @content;
  }
}

@mixin tMax {
  @media (max-width: 999px) {
    @content;
  }
}

@mixin mMax {
  @media (max-width: 768px) {
    @content;
  }
}

